import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import {API_BASE_URL} from "../config";

Vue.use(Vuex);

export const moduleCart = {
  state: {
    cart:[],
    cartData:[],
    isCartLoading:false,
    isCartLoadingFailed:false,
    isDeleting:false,
    isDeletingFailed:false,
  },
  mutations: {// /        React.  .  ,   .  .
    resetLocalCart(state){
      state.cart = [];
      state.cartData = [];
    },
    updateCartProductAmount(state, payload){
      const existingItems = state.cart.find(item=>item.productId === payload.productId);
      if (existingItems){
        existingItems.amount = payload.amount;
      }
    },
    updateCartData(state, items){
      state.cartData = items;
    },
    updateIsCartLoading(state, bool){
      state.isCartLoading = bool;
    },
    updateIsCartLoadingFailed(state, bool){
      state.isCartLoadingFailed = bool;
    },
  },
  getters: {
    cartData(state){
      return state.cartData;
    }
  },
  actions: {
    loadCart({ dispatch, commit, getters, rootGetters }){
      commit('updateIsCartLoading', true);
      return axios
        .get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: getters.userAccessKey
          },
        })
        .then(res => {
          console.log('load cart from server:',res)
          if(!getters.userAccessKey){//      ...
            localStorage.setItem('userAccessKey', res.data.user.accessKey);//...      ...
            commit('updateUserAccessKey',res.data.user.accessKey);//... .       .         context.     ,  -  localStorage.
          }
          commit('updateCartData', res.data.items);//  ...
        })
        .catch(()=>{
          commit('updateIsCartLoadingFailed', true);
        })
        .then(()=>commit('updateIsCartLoading', false))
    },
    addProductToCart({ dispatch, commit, getters, rootGetters }, {productOfferId,quantity,colorId}){//2       payload.
      return (new Promise(resolve => setTimeout(resolve,2000)))//    action (     ,  )
        .then(()=>{
          console.log('adding to cart:',productOfferId,quantity,colorId)
          return axios
            .post(`${API_BASE_URL}/api/baskets/products`, {//    
              productOfferId: productOfferId,
              colorId: colorId,
              quantity : quantity,
            },{
              params:{
                userAccessKey: rootGetters.userAccessKey//        .
              }
            })
            .then(res => {//     
              commit('updateCartData', res.data.items);//   ...
            })
        })
    },
    changeProductAmountAction({ dispatch, commit, getters, rootGetters }, {productId,amount}){
      console.log('changeProductAmountAction')
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          productId: productId,
          quantity : amount,
        },{
          params:{
            userAccessKey: rootGetters.userAccessKey
          }
        })
        .then(res => {
          commit('updateCartData', res.data.items);//   ...
          // commit('syncCartProducts');//... ,    ,      .
        })
    },
    deleteProductFromCartAction({ dispatch, commit, getters, rootGetters }, {productId}){//2       payload.
      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          data:{
            basketItemId: productId,
          },
          params:{
            userAccessKey: rootGetters.userAccessKey
          },
        })
        .then(res => {
          commit('updateCartData', res.data.items);//   ...
        })
    },
    updateCartProductAmountAction({ dispatch, commit, getters, rootGetters }, {basketItemId,quantity}){//  addProductToCart
      commit('updateCartProductAmount', {basketItemId,quantity});//           .  .
      if(quantity<1){//     
        return;
      }
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          basketItemId:basketItemId,
          quantity:quantity,
        },{
          params:{
            userAccessKey: rootGetters.userAccessKey
          }
        })
        .then(res => {
          commit('updateCartData', res.data.items);
        })
        .catch(()=>{//         (  )
          // commit('syncCartProducts');
        })

    },
  }
}
