<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <BreadCrumbItem :to="{name:'main'}" text=""/>
        <BreadCrumbItem :to="{name:'cart'}" text=""/>
        <BreadCrumbItem :to="{name:'order'}" text=" "/>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
         : {{cartData.length}} .
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <!--  -->
            <BaseFormText title="" placeholder="  " v-model="formData.name" :error="formError.name"/>
            <BaseFormText title=" " placeholder=", .  .33, " v-model="formData.address" :error="formError.address"/>
            <BaseFormText title="" placeholder="+7 999 9999999" v-model="formData.phone" :error="formError.phone"/>
            <BaseFormText title="Email" placeholder="mamail@mail.ru" v-model="formData.email" :error="formError.email"/>
            <BaseFormTextarea title="  " placeholder=" " v-model="formData.comment" :error="formError.comment"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <OptionsItem name="delivery" v-for="item in $store.getters.deliveries" :key="item.id" :value="item" :picked.sync="formData.delivery">{{item.title}}</OptionsItem>
            </ul>

            <h3 class="cart__title" v-if="computedDeliveryPrice!==' '"></h3>
            <ul class="cart__options options">
              <OptionsItem name="payment" v-for="item in $store.getters.payments" :key="item.id" :value="item" :picked.sync="formData.payments">{{item.title}}</OptionsItem>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in cartData">
              <h3>{{item.productOffer.product.mainProp.code==='color'?item.productOffer.product.title:item.productOffer.title}}</h3>
              <b>{{item.price * item.quantity}} </b>
              <span>: {{item.id}}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>{{computedDeliveryPrice}}</b></p>
            <p>: <b>{{computedPaymentMethod}}</b></p>
            <p>: <b>{{cartData.length}}</b> {{endingStr}}   <b>{{cartTotalPrice}} </b></p>
          </div>

          <button class="cart__button button button--primery" type="submit" :disabled="!formData.delivery.price||!formData.payments.title">
            <Loader v-show="status.isOrdering"/>
            <span v-show="!status.isOrdering"> </span>
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
            {{formErrorMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "../components/common/BaseFormText";
import BaseFormTextarea from "../components/common/BaseFormTextarea";
import {mapGetters} from "vuex";
import Loader from "../components/common/Loader";
import BreadCrumbItem from "../components/common/BreadCrumbItem";
import OptionsItem from "../components/common/OptionsItem";
import declOfNum from "../helpers/declOfNum";

  export default {
    components: {OptionsItem, BreadCrumbItem, BaseFormTextarea, BaseFormText, Loader},
    data(){
      return {
        formData: {// (v-model="formData.email")
          payments:{},//      
          delivery:{},
          phone:undefined,
        },
        formError:{},//     (:error="formError.phone")
        formErrorMessage: '',
        status:{
          isOrdering: false,
          isOrderingFailed: false,
        },
      }
    },
    methods: {
      order(){
        this.formError = {};
        this.formErrorMessage = '';
        this.status.isOrdering = true;
        this.$store.dispatch('makeOrder', this.formData)
          .catch((error)=>{
            this.formError = error.response.data.error.request || {};//            .
            this.formErrorMessage = error.response.data.error.message;
            this.status.isOrderingFailed = true;
          })
          .then(() => this.status.isOrdering = false);
      },
    },
    computed: {
      ...mapGetters(['cartData']),//        .
      computedDeliveryPrice(){
        if(this.formData.delivery.price==='0') return this.formData.delivery.title.toLowerCase() + ' ()'
        return this.formData.delivery.price?this.formData.delivery.price + '':' '
      },
      computedPaymentMethod(){
        return this.formData.payments.title?this.formData.payments.title:' '
      },
      endingStr(){
        return declOfNum(this.cartData.length, ['', '', ''])
      },
      cartTotalPrice(){
        return this.cartData.reduce((acc,item)=>(item.price * item.quantity)+ acc, 0);
      }
    },
    watch:{
      computedDeliveryPrice(){
        this.formData.payments={};//      
        if(!this.formData.delivery) return;
        this.$store.dispatch('loadPayments', {id:this.formData.delivery.id})
      },
    }
  };

</script>
<style>
  [v-cloak] {
    display: none;
  }
</style>

