<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        {{countProducts}} {{endingStr}}
      </span>
    </div>
    <div class="content__catalog">
      <ProductFilter/>
      <section class="catalog">
        <div style="margin: 0 auto">
          <Loader v-if="productsLoading"/>
          <div v-if="productsLoadingFailed">
            Network error
            <button @click.prevent="loadProducts">try again</button>
          </div>
        </div>
        <ProductList :products="products"/>
        <BasePagination v-model="$store.getters.filter.page" :total-items="countProducts" :per-page="$store.getters.filter.productsPerPage"/>
      </section>
    </div>
  </main>
</template>

<script>

  import ProductList from "../components/product/ProductList.vue";
  import Header from './../components/Header.vue';
  import Footer from './../components/Footer.vue';
  import BasePagination from '../components/common/BasePagination.vue';
  import ProductFilter from "../components/product/ProductFilter";
  import Loader from "../components/common/Loader";
  import {mapActions} from "vuex";
  import declOfNum from "../helpers/declOfNum";

  export default {
    components: {
      ProductList,
      Header,
      BasePagination,
      Footer,
      ProductFilter,
      Loader,
    },
    data() {// 
      return {
        productsLoading: false,
        productsLoadingFailed: false,
        memoryList:[],
        productsData: null,
      };
    },
    computed: {//     ,      .
      products() {
        // console.log('computing products:', this.productsData ? this.productsData.items : [])
        return this.productsData ? this.productsData.items : [];
      },
      countProducts() {
        return this.productsData ? this.productsData.pagination.total : 0;//       productsData (    created)   .
      },
      endingStr(){
        return declOfNum(this.countProducts, ['', '', ''])
      }
    },
    methods: {
      // ...mapActions(['loadProductsAction']),//                 .
      loadProducts(){//      .
        this.productsLoading=true;
        this.productsLoadingFailed=false;
        clearTimeout(this.loadProductsTimer);//.    3 (3     = 3 )   1 - .      .     .
        this.loadProductsTimer = setTimeout(()=>{// .    - ( )(     , )
          this.$store.dispatch('loadProductsAction')
          .then(res => this.productsData = res.data)//     .
            .catch((error)=>this.productsLoadingFailed=true)
          .then(()=>this.productsLoading=false);
        },0)
      },
    },
    watch: {//     -> 
      page() {//  ,  page =  .
        this.loadProducts();
      },
      '$store.getters.filter': {
        handler: function () {
          this.loadProducts()
        },
        deep: true
      },
    },
    created(){// componentDidMount.
        this.loadProducts();
    }
  };
</script>





