<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{name:'main'}" class="breadcrumbs__link">
            
          </router-link>        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{name:'cart'}" class="breadcrumbs__link">
            
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
         : {{cartData.length}} .
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in cartData" :key="item.id" :item="item"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            &nbsp;   &nbsp; 
          </p>
          <p class="cart__price">
            : <span>{{cartTotalPrice | numberFormat}} </span>
          </p>

          <router-link :to="{name:'order'}" tag="button" class="cart__button button button--primery" type="submit" v-if="cartData.length">
               
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "../helpers/numberFormat";
import {mapGetters} from "vuex";
import CartItem from "../components/cart/CartItem";

  export default {
    components: {CartItem},
    filters: {// html -> {{product.price | numberFormat}}  -       .  Vue.   computed.
      numberFormat,
    },
    computed: {
      ...mapGetters(['cartData']),
      cartTotalPrice(){
        return this.cartData.reduce((acc,item)=>(item.price * item.quantity)+ acc, 0);
      }
    },
  };

</script>




